    box-sizing: border-box;
    /*border: 1px solid black;*/
}

body {
    padding: 24px;
    margin: 0px;

    width: 100vw;
    height: 100vh;
}

/* General */
#today,
#current,
#details,
.next-day,
.next-hour {
    border-radius: 16px;
}

#header,
#today,
.next-day,
.next-hour {
    box-shadow: 2px 2px 0.4px rgb(210, 247, 255, 0.2);
}

.next-day,
.next-hour {
    padding: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;

    background-color: rgba(104, 121, 208, 0.4);
}

.spacer {
    flex: 1 1 auto;
}

.icon-target.snow {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.icon-target.rain {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.icon-target.fog {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

.icon-target.wind {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

.icon-target.cloudy {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

.icon-target.partly-cloudy-day {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

.icon-target.partly-cloudy-night {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

.icon-target.clear-day {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
}
.icon-target.clear-night {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
}

/* Main */
#page {
    width: 100%;
    height: 100%;
    padding: 16px;
    border-radius: 24px;

    display: flex;
    flex-direction: column;
    gap: 8px;

    background-color: lightskyblue;
}

#header {
    padding: 0 12px;
    border-radius: 12px;

    display: flex;
    align-items: center;

    h2 {
        display: inline;

        margin: 0;
        font-size: 1.8rem;
    }
}

#comment {
    align-self: flex-end;
    padding: 4px 8px;

    color: dimgray;
}

#options {
    display: flex;
    justify-content: space-evenly;
    align-items: stretch;
    gap: 8px;

    button {
        width: 24px;
        border: 0px;

        background-color: transparent;
        background-repeat: no-repeat;
        background-size: cover;
    }
}

#toggle-hour {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
}

#toggle-hour.toggled {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}

#toggle-temp {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}

#toggle-temp.toggled {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_12___});
}

#search {
    position: relative;
}

#search > input {
    width: 12rem;
    padding: 0px 2.2rem 0px 1.1rem;
    border-radius: 12px;
    border: 0px;

    font-size: 1.1rem;
}

#search > button {
    height: 100%;

    position: absolute;
    top: 0%;
    right: 0.5rem;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_13___});
    background-size: cover;
}

#error {
    display: none;
    position: absolute;
    right: 0;

    border-radius: 12px;
    padding: 8px;
    margin: 8px 0px;

    white-space: nowrap;

    background-color: orangered;
    color: white;
}

#error.show {
    display: block;
}

#weather {
    flex: 1 auto;

    display: flex;
    gap: 8px;
}

#today {
    flex: 5 0.2 auto;

    display: flex;
    flex-direction: column;

    background-size: cover;
    background-repeat: no-repeat;

    box-shadow: 0 0 8px 8px lightskyblue inset;

    color: white;
}

#details {
    flex: 1 auto;
}

#description {
    padding: 20px 24px;
    border-radius: 12px;

    display: flex;
    justify-content: space-between;

    background-image: linear-gradient(
        rgba(105, 105, 105, 0.4),
        rgba(105, 105, 105, 0.3),
        transparent
    );
}

#date {
    font-size: 1rem;
    font-weight: bold;
}

#time {
    font-size: 2rem;
}

#credits {
    padding: 2px;

    font-size: 0.9rem;

    color: ghostwhite;
    background-color: rgba(105, 105, 105, 0.6);

    a:link {
        color: lightgray;
    }
}

#details {
    padding: 12px 24px;

    display: flex;
    align-items: flex-end;
    gap: 8px;

    background-image: linear-gradient(transparent, rgba(104, 121, 208, 0.8));
}

#weather-icon {
    height: 144px;
    aspect-ratio: 1 / 1;

    background-size: cover;
    filter: invert(1);
}

#temperature {
    display: flex;
}

#temp-container {
    align-self: center;

    display: flex;
    flex-direction: column;
}

#temp-real {
    font-size: 5rem;
    line-height: 4.2rem;
}

#temp-unit {
    font-size: 3.6rem;
    line-height: 3.2rem;
}

#current {
    flex: 5 0.2 auto;
}

#condition {
    font-size: 2rem;
}

.next-hour {
    flex: 1 1 min(10%, 96px);
    height: 30%;

    .next-icon {
        filter: invert(1);
    }
}

.next-day {
    flex: 1 1 min(10%, 96px);
}

.next-dayname,
.next-date {
    font-weight: bold;
}

.icon-container {
    flex: 1 auto;
    align-self: stretch;

    display: flex;
    justify-content: center;
    align-items: center;
}

.next-icon {
    flex: 1 auto;

    aspect-ratio: 1 / 1;
    margin: 4px;

    background-size: cover;
}

.next-temp {
    flex-basis: 17%;
    font-size: 2.4rem;
}

.next-hour > .next-temp {
    font-size: 1.5rem;
}
